$navH: 4rem;
$cardW: 200px;

@mixin absoluteFill {
	position: absolute;
	width: 100%;
	height: 100%;
}
@mixin flexColumnCenter {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

@keyframes fadeIn {
	from {
		transform: translateY(20px);
		opacity: 0;
	}
	to {
		transform: translateY(0);
		opacity: 1;
	}
}

nav {
	color: white;
	background-color: #00000075;
	position: fixed;
	height: $navH;
	width: 100%;
	z-index: 10;
	padding-left: .5rem;
	transition: height .5s ease;
	overflow: hidden;

	&.menuOpen {
		height: $navH * 4;
	}

	& > div {
		display: flex; align-items: center; height: $navH;
	}

}

.card {
	position: relative;
	width: $cardW;
	height: $cardW;
	margin-top: 2 * $navH;
	z-index: 2;
	animation: fadeIn .6s ease-in-out;
	& > div {
		@include absoluteFill;
		transform-style: preserve-3d;
		transition: transform .5s ease;
		& > div:first-of-type {
			@include absoluteFill;
			backface-visibility: hidden;
			img {
				@include absoluteFill;
				border-radius: 50%;
			}
		}
		& > div:nth-of-type(2) {
			@include absoluteFill;
			@include flexColumnCenter;
			transform: rotateY(180deg);
			backface-visibility: hidden;
			background-color: Lavender;
			border-radius: 10%;
			line-height: 2rem;
		}
	}
	&:hover > div {
		transform: rotateY(180deg);
	}
}

.header {
	color: white;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	background: url('https://res.cloudinary.com/vg9lab/image/upload/w_750,h_1500,c_fill/v1641757052/samples/landscapes/stars_mqcqjj.jpg') center center/cover;
	.headerTitle {
		margin-top: 1.5rem;
		max-width: $cardW * 1.1;
		text-align: center;
		line-height: 1.5rem;
		opacity: 0;
		animation: fadeIn .6s ease-in-out;
		animation-delay: .5s;
		animation-fill-mode: forwards;
		z-index: 2;
	}
	&::after {
		content: '';
		@include absoluteFill;
		background-color: #00000075;
		z-index: 1;
	}
}

.footer {
	height: $navH;
}
